@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap");

*{
  box-sizing: border-box;
  margin: 0; 
  padding: 0;
}
body {
  font-family: "outfit", 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p{
  font-size: 1.6rem;
}
h1{
  font-size: 6rem;
}
.app{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #ebeced;
}
.app::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(./assets/pexels-stywo-1261728.jpg) no-repeat center center/cover;
  z-index: -1;
}
.app .input {
  text-align: center;
  padding: 1rem;
  z-index: 200;
}
.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.8);
  background-color: rgba(255, 255,255,0.1);
  outline: none;
  color: #ebeced;
}
.app input:focus{
  outline: #ebeced 1px solid;
  transition: 0.3s;
}
::placeholder{
  color: #ebeced;
}
.container{
  width: 700px;
  height: 700px;
  position: relative;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 5%;
  margin: auto;
  backdrop-filter: blur(8px);
  border-radius: 15px;
  border: 1px solid #ebeced;
}

.app .top {
  width: 100%;
  margin: 1rem auto;
}
.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269.5deg);
}
.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  align-items: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2);
}
.bold{
  font-weight: 700;
}
